<template>
    <div style="width:400px;">
        <el-form
            v-loading="isLoading"
            ref="stuForm"
            label-width="100px"
            :model="student"
            :rules="rules"
        >
            <el-form-item
                label="学生姓名"
                prop="name"
            >
                <el-input v-model="student.name"></el-input>
            </el-form-item>
            <el-form-item label="学生性别">
                <el-switch
                    v-model="student.gender"
                    active-text="男"
                    inactive-text="女"
                    active-value="男"
                    inactive-value="女"
                ></el-switch>
            </el-form-item>
            <el-form-item
                label="学生年龄"
                prop="age"
            >
                <el-input
                    v-model.number="student.age"
                    type="number"
                ></el-input>
            </el-form-item>
            <el-form-item
                label="电话号码"
                prop="phone"
            >
                <el-input v-model="student.phone"></el-input>
            </el-form-item>
            <el-form-item
                label="家庭住址"
                prop="address"
            >
                <el-input v-model="student.address"></el-input>
            </el-form-item>
            <el-form-item
                label="所属班级"
                prop="class"
            >
                <el-select
                    v-model="student.class"
                    placeholder="请选择班级"
                >
                    <el-option
                        v-for="item in classes"
                        :key="item._id"
                        :label="item.name"
                        :value="item._id"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item
                label="所学课程"
                prop="course"
            >
                <el-select
                    v-model="student.course"
                    placeholder="请选择课程"
                >
                    <el-option
                        v-for="item in courses"
                        :key="item._id"
                        :label="item.name"
                        :value="item._id"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-button
                    type="primary"
                    @click="handleSubmit"
                >提交</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
export default {
    props: ["sid"],
    data() {
        return {
            student: {
                name: "",
                class: "",
                course: "",
                gender: "男",
                phone: "",
                age: "",
                address: ""
            },
            isLoading: false,
            rules: {
                name: [
                    {
                        required: true,
                        message: "请输入学生姓名",
                        trigger: "change"
                    },
                    {
                        min: 2,
                        max: 5,
                        message: "姓名字符在 2 到 5 个字符之间",
                        trigger: "change"
                    }
                ],
                age: [
                    {
                        required: true,
                        message: "请输入学生年龄",
                        trigger: "change"
                    }
                ],
                phone: [
                    {
                        required: true,
                        message: "请输入电话号码",
                        trigger: "change"
                    },
                    {
                        validator: function(rule, value, callback) {
                            if (/^1\d{10}$/.test(value)) {
                                callback(); //通过验证
                            } else {
                                callback(
                                    new Error("手机号必须是1开头的11位数字")
                                );
                            }
                        },
                        trigger: "change"
                    }
                ],
                address: [
                    {
                        required: true,
                        message: "请输入家庭住址",
                        trigger: "change"
                    }
                ],
                class: [
                    {
                        required: true,
                        message: "请选择所在班级",
                        trigger: "change"
                    }
                ],
                course: [
                    {
                        required: true,
                        message: "请选择所属课程",
                        trigger: "change"
                    }
                ]
            }
        };
    },
    computed: {
        courses: {
            get() {
                //返回仓库中对应的课程数据
                return this.$store.state.course.data;
            },
            set(val){
                this.$store.commit("course/setData", val);
            }
        },
        classes: {
            get() {
                //返回仓库中对应的课程数据
                return this.$store.state.class.data;
            },
            set(val){
                this.$store.commit("class/setData", val);
            }
        }
    },
    async mounted() {
        this.isLoading = true;
        if (this.sid) {
            //读取学生对象
            let resp = await fetch("/api/student/" + this.sid);
            resp = await resp.json();
            this.student = resp;
        }

        this.$store.dispatch("course/fetch");
        this.$store.dispatch("class/fetch");
        this.isLoading = false;
    },
    methods: {
        async handleSubmit() {
            //触发表单验证
            try {
                await this.$refs.stuForm.validate();
                //抛出事件
                this.$emit("submit", this.student);
            } catch {
                //验证失败
                console.log("验证失败");
            }
        }
    }
};
</script>

<style scoped></style>