<template>
  <div>
    <h1>修改学生</h1>
    <add-or-edit :sid="id" @submit="handleSubmit"></add-or-edit>
  </div>
</template>

<script>
import AddOrEdit from "./components/AddOrEdit";
export default {
  components: {
    AddOrEdit
  },
  data() {
      return {
          id: this.$route.params.id
      }
  },
  methods: {
    async handleSubmit(stu) {
      await fetch("/api/student/"+this.id, {
        method: "put",
        body: JSON.stringify(stu),
        headers: {
          "content-type": "application/json"
        }
      });
      this.$message({
        message: "修改学生成功",
        type: "success"
      });
    }
  }
};
</script>